import React, { Component} from 'react';
import { Select } from "react-select-virtualized";
import { Button, Spinner, Container, Row, Form, FormGroup, Input, Col, Badge, CustomInput, Table, Popover, PopoverHeader, PopoverBody} from 'reactstrap';
import {getAdmins, changePass, getOgs, getLgId, fetchUsers} from '../../apiCall/airwatch';
import generator from 'generate-password';
import _ from 'lodash';


 
class Admin extends Component {
  _isMounted = false;
  state = {
    selectedOptionAdmin: null,
    selectedOptionOg: null,
    admins: [],
    ogs: [],
    loading: false,
    loadingOg: false,
    pass:'',
    passwords:[],
    color: 'red',
    checked: false,
    lgtoogle: false,
    lgid:'',
    popoverOpen: false,
    users: []
   
  };

  togglePopOver = () =>{
    this.setState({
      popoverOpen: !this.state.popoverOpen
    });
  }


  componentDidMount() {
    this._isMounted = true;

    this.setState({ loading: true, loadingOg: true}, async () => {




    await getAdmins(this.props.email, this.props.sub).then(admin=>
      {
      console.log("admin ", admin);
        if (_.isString(admin.data) && admin.data.startsWith("Please56785678")){
          this.setState({loading: false ,
            message: "You need to go to Settings and set up your credentials"});
            }
        else {
        if (this._isMounted){
        console.log("admin data ", admin.data)
        if (Array.isArray(admin.data)){
        const newadmins = admin.data.map( item => {
          const { UserName: label, Uuid: value, ...rest } = item;
          return { label,value, ...rest }
         }
        );  
        console.log("newadmins", newadmins);
        this.setState({admins: newadmins,
          loading: false
       })
      
        } else {
          
          this.setState({
            loading: false,
         message: admin.data})
        }
      }
      }
    })

    await getOgs(this.props.email, this.props.sub).then(og=>
      {
        
        console.log("og ", og);
        if (_.isString(og.data) && og.data.startsWith("Please56785678")){
          this.setState({loadingOg: false ,
            messageOg: "You need to go to Settings and set up your credentials"});
            }
        else {
        
        if (this._isMounted){
        console.log("og data ", og.data)
        if (Array.isArray(og.data)){

          const newogs = og.data.map( item => {
            const { Name: label, Name: value, ...rest } = item;
            return { label,value, ...rest }
           }
          );  
          console.log("newogs", newogs);
        
     


        this.setState({ogs: newogs,
          loadingOg: false
       })


      
        } else {
          
          this.setState({
            loadingOg: false,
         messageOg: og.data})
        }
      }
    }
    })
  
    })
  }

  componentWillUnmount() {
    this._isMounted = false;
  }


  toggleClass() {
    const currentState = this.state.checked;
    this.setState({ checked: !currentState });
};

  handleChangeAdmin = selectedOptionAdmin => {
    this.setState({ selectedOptionAdmin,  messageLg:'',
    message:'',
    messageOg:'' });
    console.log(`Option selected:`, selectedOptionAdmin);
  };
  handleChangeOgs = async selectedOptionOg => {
    this.setState({ 
      users: [],
      popoverOpen: false
    });
    if (selectedOptionOg){
      this.setState({ loadingOg : true})
    const users = await fetchUsers(this.props.email, this.props.sub,selectedOptionOg.Id.Value)
    this.setState({ 
      users: users.data
    });
    }
    
    this.setState({ 
      selectedOptionOg,  messageLg:'',
    message:'',
    messageOg:'',
    loadingOg : false });
    console.log(`Option selected:`, selectedOptionOg);
  };


  handleChangepass = evt => {
    this.setState({ pass: evt.target.value, messageLg:'',
    message:'',
    messageOg:'' });
    console.log("pass:", this.state.pass);
  };



  handleRandomPass = (e) => {
    this.toggleClass();
    if(e.target.checked) {
      var passwords = generator.generateMultiple(5, {
        length: 12,
        symbols: false,
        numbers: true,
        uppercase: true,
        strict: true
    });

    passwords = passwords.map(i =>  i +'Test12!'
    
    );
    this.setState({passwords,
    message:''})
      console.log(passwords);
  
    } else {this.setState({passwords: [], message:''}, () =>console.log("passwords unchek", this.state.passwords))
    }
  };

  handleChangeLgId = (evt) => {
    const {value,name} = evt.target;
    console.log("event target ", name)
    this.setState({
      lgtoogle: false,
      lgid: value,
      messageLg:'',
      message:'',
      messageOg:''
    });  
    
  }


onSubmitLgId= async (e)=>{
  e.preventDefault();
  const lgids = await getLgId(this.props.email, this.props.sub, this.state.lgid);

  if (_.isString(lgids.data) && lgids.data.startsWith("Please56785678")){
    this.setState({lgtoogle: false ,
      messageLg: "You need to go to Settings and set up your credentials"});
      }
  else {

  if (_.isString(lgids.data)){
    this.setState({messageLg: lgids.data,
      lgtoogle:false,
      color: 'red'})
  }
  else{this.setState({lgids: lgids.data,
  lgtoogle:true,
  messageLg:''})
  console.log("lgids ", lgids.data)
  }
  }  
}

  onSubmit=(e)=>{
    e.preventDefault();
    console.log("onsubmit pass", this.state);
    let mess ='';
    //console.log(this.state.pass);
    console.log(this.state.selectedOptionAdmin.Id.Value)
    this.setState({ loading: true}, async () => {
      const passes =  this.state.passwords;
      console.log("passes ",passes);
      console.log("pass ", this.state.pass)
      passes.push(this.state.pass);
  //    passes.unshift(this.state.pass);
      console.log("all ",passes)

     for (var i =0; i<passes.length; i++){
       await changePass(this.props.email, this.props.sub, this.state.selectedOptionAdmin.Id.Value, passes[i]).then(
        pass =>{ console.log("pass", pass.data+" "+i);
      mess = pass.data;
      if (pass.data === "OK") {this.setState({color:"green"}) }else {this.setState({color:"red"})};
      
      }
        )
   //    if (mess.includes("complexity")) {break}; 
     }
   /*   passes.forEach(async (item, i)=>{
        mes = await changePass(this.props.email, this.props.sub, this.state.selectedOption.Id.Value, item).then(
        pass =>{ console.log("pass", pass.data+" "+i);
        if (pass.data === "OK") {this.setState({color:"green"}) }else this.setState({color:"red"});
         this.setState({loading: false,
        message: pass.data})}
        )
         }) */
        
         this.setState(
           
           {loading:false,
            message: mess,
            checked: false,
             passwords: []},
             )
      })  
  }

  render() {
    console.log("users render", this.state.users);

    

    const { selectedOptionAdmin, selectedOptionOg, lgids } = this.state;

    const customStyles = {
      control: (base, state) => ({
        ...base,
        border: '1px solid rgb(116, 135, 141)',
        boxShadow: 'none',
        '&:hover': {
            border: '1px solid rgb(76, 81, 82)' , 
        }
    })
};

    return (
      <Container>
        <Row>
        <Col sm="4">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
      <Col sm="4">
      <h5 className="mb-3 title">Basic Admin password reset</h5>
      <Select
       placeholder ="Select username..."
        value={selectedOptionAdmin}
        onChange={this.handleChangeAdmin}
        options={this.state.admins}
        
        styles = { customStyles } 
         
      />

      <Row className="mx-auto mt-3">
      {this.state.loading ? <Spinner className="mx-auto" animation="border" variant="warning" /> : "" }
      </Row>
       </Col>
      </Row>
     
  
  
    <Form className="mt-2"onSubmit={this.onSubmit} >  
    
   
    <Row>
           <CustomInput type ="checkbox" className="ml-3 mb-4" id= "smartgroup"  onChange={this.handleRandomPass} checked={this.state.checked} label={ <span><Badge color="secondary" >Check to bypass remembered password policy and reuse same password</Badge></span>}/>
           </Row>
    <Row>    
    <Col sm="4">
        <FormGroup>
          
                <Input type="text" className="form-control" id="awpass" name="awpass" value={this.state.pass} onChange={this.handleChangepass} placeholder="Password..."/>
                </ FormGroup > 
            </Col>
         <Col sm="2">
       <Input type="submit" value="Submit" className="btn-sm  btn-info"  />
       </Col>
       </Row>
       </Form >
       <Row>
      <h2 style ={{color: this.state.color}}>{this.state.message}</h2> 
      </Row>
      <Col sm="4">
      <hr className="style-two"></hr>
      </Col>
      <h5 className="mb-3 title">Get OG details per Name</h5>
           <Row>
      <Col sm="4">
       
      <Select
       placeholder ="Select OG name..."
        value={selectedOptionOg}
        onChange={this.handleChangeOgs}
        options={this.state.ogs}
        styles = { customStyles }      
      />

      <Row className="mx-auto mt-3">
      {this.state.loadingOg ? <Spinner className="mx-auto" animation="border" variant="warning" /> : "" }
      </Row>
       </Col>


      </Row>

      <Row>
      <h2 style ={{color: this.state.color}}>{this.state.messageOg}</h2> 
      </Row>
      {(selectedOptionOg)? 
      <Table bordered>
      <thead>
        <tr>
          <th>Group Id</th>
          <th>Lgid</th>
          <th>Type</th>
          <th>Users</th>
          <th>Devices</th>
          <th>Admins</th>      
        </tr>
      </thead>
      <tbody>
      <tr>   
          <td>{selectedOptionOg.GroupId}</td>
          <td>{selectedOptionOg.Id.Value}</td>
          <td>{selectedOptionOg.LocationGroupType}</td>
         
          <td>
        
      <Button id="Popover1" type="button" color="primary" block size="sm">
      {selectedOptionOg.Users}
      </Button>
      {(this.state.users)?
      <Popover placement="right" isOpen={this.state.popoverOpen} target="Popover1" toggle={this.togglePopOver}>
        <PopoverHeader>Usernames</PopoverHeader>
        <PopoverBody>
          <ul className="nobull pl-0">
          {this.state.users.map((item,i) => <li key={i}>{item.UserName}</li>)}
          </ul>
        </PopoverBody>
      </Popover>:''}
      
            </td>
          
          
          <td>{selectedOptionOg.Devices}</td>
          <td>{selectedOptionOg.Admins}</td>
        </tr>
      
      </tbody>
    </Table>
    : ""}
   
   
   
   <Col sm="4">
      <hr className="style-two"></hr>
      </Col>
    <Form onSubmit={this.onSubmitLgId}>
    <h5 className="mb-3 title">Get OG details per lgid</h5>
  <Row>
    <Col md="4" >
      
       <FormGroup> 
                <Input type="number" maxLength="8" className="form-control" id="lgid" name ="lgid" value={this.state.lgid}  onChange={this.handleChangeLgId} required placeholder="Insert lgid..."/>
                </ FormGroup >
                </Col>
                <Col md={2}> 
                <FormGroup>
               
                <Input type="submit" value="Submit" id="awlg" name="awlg" className="btn-sm btn-block btn-info"  />
               

              </FormGroup >
              </Col>
              </Row>
</Form>
<Row>
      <h2 style ={{color: this.state.color}}>{this.state.messageLg}</h2> 
      </Row>

    <Row>
      {(this.state.lgtoogle)? 
      <Table bordered>
      <thead>
        <tr>
          <th>OG Name</th>
          <th>Group Id</th>
          <th>Lg level</th>
          <th>Users</th>
          <th>Devices</th>
          <th>Admins</th>      
        </tr>
      </thead>
      <tbody>
      <tr>   
          <td>{lgids.Name}</td>
          <td>{lgids.GroupId}</td>
          <td>{lgids.LgLevel}</td>
          <td>{lgids.Users}</td>
          <td>{lgids.Devices}</td>
          <td>{lgids.Admins}</td>
        </tr>
      </tbody>
    </Table>
    : ""}
    </Row>
 
     
    </Container>
    );
  }
}

export default Admin;
