import React, {useState, useEffect} from 'react';
import { Row, Col, Form, FormGroup, Input, Spinner} from 'reactstrap';
import { Select } from "react-select-virtualized";
import {fetchUsers} from './../../apiCall/airwatch';
import { useAuth0 } from "../../Auth0/react-auth0-spa";
import _ from 'lodash';



function UserSearch(props) {

  const [state, setState] = useState({
    users: [],
    loading: false,
    message: ''   
  })

  const { user } = useAuth0();
  const fetchDev= async ()=>{
    
     await fetchUsers(user.email, user.sub).then(
      user => { 
        if (_.isString(user.data) && user.data.startsWith("Please56785678")){
          props.addmessage("You need to go to Settings and set up your credentials")
          setState({
            ...state,
            loading: false 
           });
            }
        else {
      let newadmins = user.data.map( item => {
          const {  Uuid: value, ...rest } = item;
          const label = `${item.UserName} - ${item.Group}`;
          return { label,value, ...rest }
         })
        
        
        
      setState({...state,
        users: newadmins,
        loading: false})
      }
      }
    )}
  
  
  useEffect( () => { 
    setState({...state,
      loading: true})
    fetchDev() }, [] );


  return (
    <Form onSubmit={props.onSubmit} > 
    <Row form>
    <Col md={4}>
    <FormGroup> 
    <Select
       placeholder ="Select username..."
        value={props.selectedOption}
        onChange={props.handleChange}
        options={state.users}
        
         
      />
    
    </FormGroup >
    <Row className="mx-auto mt-3">
   {state.loading ? <Spinner className="mx-auto" animation="border" variant="warning" /> : "" }
   </Row>
    </Col>
    <Col md={2}> 
    <FormGroup>
    <Input type="submit" value="Submit" id="awbut" name="awbut" className="btn-sm btn-block btn-info"  disabled ={props.disable} />
    </FormGroup >
     </Col>
    
    </Row>
    
   </Form >
   
  )
  
}

export default UserSearch