import React, { Component } from 'react';
import {Button, Spinner, Container, Row, Form, FormGroup, Input, Col} from 'reactstrap';
import dateFormat from 'dateformat';
import {getToken, axiosListReportRec, searchAllReports, axiosGetLink} from '../../apiCall/intelli';
import _ from 'lodash';

class Report extends Component {

  _isMounted = false;

    state = {
      reports: [],
      reportId:'',
      link: '' ,
      reportDownload: [] ,
      date: '',
      id: '' ,
      message: '' ,
      disabled: false,
      time:'',
      loading: false,
      token:'',
      name:''
      
    };
     
   
  toggleClass() {
    const currentState = this.state.disabled;
    this.setState({ disabled: !currentState });
};

    



    async componentDidMount(){
      this._isMounted = true;
      

    this.setState({ loading: true}, async () => {

     
       const token = await getToken(this.props.email,this.props.sub);

       console.log("token", token)

       if (token.startsWith("Please56785678")){
        this.setState({loading: false ,
          message: "You need to go to Settings and set up your credentials"});
          }
      

       else{

        if (this._isMounted){
       this.setState({token});
       
       
          const reporting = await searchAllReports(this.props.email,this.props.sub, this.state.token); 
          console.log("err reporting", reporting.data)
          if (_.isString(reporting.data) ){
            this.setState({loading: false ,
              message: reporting.data});
              }
          else {
           let allReports =  reporting.data.map( el => { return {name: el.name, id: el.id}} );

           this.setState({reports: allReports,
            loading: false });
           console.log("all report", allReports)
          }



           }
          }
  })
} 

componentWillUnmount() {
  this._isMounted = false;
}

    
    checkStatusCompleted = () => {
      
    
    const completedReports = this.state.reportDownload.filter(i => i.status === "COMPLETED").reduce(function (a, b) { return a.created_at > b.created_at  ? a : b; });
    
   // console.log("completedReport ", completedReports);

     
             
             let date = dateFormat(completedReports.created_at, "mediumDate");
             let time = dateFormat(completedReports.created_at, "shortTime");
             
             
             this.setState({date: date,
                            time: time,
                             id: completedReports.id
                            });
                              
    }

    handleChange = async (e) => {
      //console.log("vaue e: ", e.target.value);
      if (e.target.value !== '-1'){
      const optionsAr = this.state.reports.map(v => ({
        name: v.name,
        id: v.id
      }));
      
     const detect = optionsAr.filter(v => (v.id === e.target.value));
     //console.log("detect ",detect);
     
      this.setState({ reportId: e.target.value,
                       name: detect[0].name,
                       
       }   );
       this.toggleClass();
       
      } else {

      

      }
      
      
    }
  
    onSubmit = async (e) => {
      e.preventDefault();

      this.setState({ loading: true}, async () => {

    const linksReport= await axiosListReportRec(this.props.email,this.props.sub, this.state.token, this.state.reportId);
      console.log("linksReportformex: ", linksReport);
  this.setState({reportDownload: linksReport
  });
  //console.log("stateDidUpdateformex: ", this.state); 
  //console.log('id: ', this.state.reportId);
  this.toggleClass();
  console.log("isEnabled ", this.state.disabled);
  
  this.checkStatusCompleted();
  
 const linkReport = await axiosGetLink(this.props.email,this.props.sub, this.state.token, this.state.id);
 // console.log("my stateLINK2: ",linkReport);
      this.setState(
        {link: linkReport,
       loading: false}
       )
    

     this.setState(
      {
     loading: false}
     )
    }
    )

  }


  

    render() {
      
    let loading = this.state.loading;
      return (
        
        <Container>
           <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
           <Col sm="8" className="text-center">
      <h5 className="mb-3 title" style={{textAlign: "center"}}>Download individual report from WS1 Intelligence</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>


          
          
        <Form onSubmit={this.onSubmit}>
       
        <Col sm="8">
          <FormGroup>
        
        <Input type ="select" className="form-control mt-5" onChange={this.handleChange} disabled={this.state.disabled} >
          <option value='-1'>select your report</option>
        {this.state.reports.map((v) => <option key={v.id} value={v.id} name={v.name}>{v.name}</option>)}
    
        </Input>
        </FormGroup>
        <div className="mt-4 mb-4">
        <Button type="submit"  variant="success" size="sm" block disabled={!this.state.disabled}>Create Report Link</Button>
        </div>
        </Col>
      </Form> 
       
         <Row >
         <Col sm="8" className="text-center">
      {loading ? <Spinner className="mx-auto" animation="border" variant="warning" /> : "" }
      {(this.state.link && !this.state.disabled) ? <Button  variant="outline-dark" size="sm" block href={this.state.link}>{`Download "${this.state.name}" issued ${this.state.date} at ${this.state.time}`}</Button>: ""}
      
      </Col>
      
      </Row>
      
     
      
      <Row>
      <h2>{this.state.message}</h2> 
      </Row>
      
     
      </Container>
     
      );
    }
  }

  export default Report;