

import * as ActionTypes from '../actions/ActionTypes';
import { lake1Creds} from './forms';

export default function (state = lake1Creds, action){
    switch(action.type){
        case ActionTypes.LAKE1_SELECTED:
          
            return Object.assign({},state, action.payload)

            case ActionTypes.LAKE1_RETRIEVED:
                    
                    return Object.assign({},state, action.payload)

        default:
            return state;
    }
    
}