

import * as ActionTypes from '../actions/ActionTypes';
import {googles} from './forms';

export default function (state = googles, action){
    switch(action.type){
        case ActionTypes.GOOGLE_SELECTED:
          
            return Object.assign({},state, action.payload)

            case ActionTypes.GOOGLE_RETRIEVED:
                    
                    return Object.assign({},state, action.payload)

        default:
            return state;
    }
    
}