

import * as ActionTypes from '../actions/ActionTypes';
import { lake2Creds} from './forms';

export default function (state = lake2Creds, action){
    switch(action.type){
        case ActionTypes.LAKE2_SELECTED:
          
            return Object.assign({},state, action.payload)

            case ActionTypes.LAKE2_RETRIEVED:
                    
                    return Object.assign({},state, action.payload)

        default:
            return state;
    }
    
}