import React, { Component } from "react";
import axios from 'axios';
import {SERVER_URL} from '../../config';

import {
  
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Col,
  Container,
  Row

} from "reactstrap";


class Contact extends Component {
  constructor(props) {
    super(props);

    this.state = {
      
     disabled: true,
      feedback: '',
      message: ""
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value,
      message:''
    });
    if (name ==="object" && value !== '-1'){
      this.setState({
        disabled: false
      });
    }
    else if (name ==="object" && value === '-1') this.setState({
      disabled: true
    });
  }



  sendFeedback = async (email, name, object, feedback) => {

    
    const url = `${SERVER_URL}/contact/send`;
     
    const data ={
      email,
      name,
      object,
      feedback

     
    }
    
  try { const feed = await axios.post(url, data); 
    
    if (feed.data === "your message has been sent")
      this.setState({
        color: "green",
        message: feed.data});
       else  this.setState({
        color: "red",
        message: feed.data});
        console.log("fromapi ",feed)
      }

   catch(err){
   console.log(err);
    
    }
    
}

  handleSubmit(event) {
    event.preventDefault();
     
    this.sendFeedback(this.props.email, this.props.name, this.state.object, this.state.feedback)

    this.setState({feedback: '',})
    
  }
  

    
  

  render() {
    
    return (
      <Container>
       
      <Row>
       <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
           <Col sm="8" className="text-center">
      <h5 className="mb-3 title" style={{textAlign: "center"}}>Send us your Feedback, {this.props.name}</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>

        <div className="row row-content">
          



          
          <div className="col-12 col-md-9  mt-5">
            <Form onSubmit={this.handleSubmit}>
             
             
              <FormGroup row>
                <Label htmlFor="object" md={2}>
                  Object:
                </Label>
                <Col md={10}>
                  <Input
                    type="select"
                    id="object"
                    name="object"
                   
                    onChange={this.handleInputChange}
                  >
                     <option name="action" value='-1'>Select subject...</option>
                     <option name="action" value='bug'>Is it about a Bug?</option>
                      <option name="action" value='support'>Does it concern support?</option>
                      <option name="action" value='feature request'>Do you want a new feature?</option>
                      <option name="action" value='other'>Any other subject</option>
                    </Input>
                  
                </Col>
              </FormGroup>
             
             
              <FormGroup row>
                <Label htmlFor="feedback" md={2}>
                  Description:
                </Label>
                <Col md={10}>
                  <Input
                    type="textarea"
                    id="feedback"
                    name="feedback"
                    minLength="10"
                    rows="12"
                    value={this.state.feedback}
                    onChange={this.handleInputChange}
                    required
                  ></Input>
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col md={{ size: 10, offset: 2 }}>
                  <Button type="submit" color="primary" disabled={this.state.disabled}>
                    Send Feedback
                  </Button>
                  <h2 style ={{color: this.state.color}} className="mt-3">{this.state.message}</h2>
                </Col>
              </FormGroup>
            </Form>
          </div>
        </div>
        <Row>
       
      </Row>
      
        
      </Container>
    );
  }
}

export default Contact;