import React, { Component} from 'react';
import {Container} from 'reactstrap';
import dateFormat from 'dateformat';




class DeviceInfo extends Component {
  
 render() {
  const data = this.props.deviceData.data
  // const data = this.props.deviceData.data.filter(item => item.EnrollmentStatus === "Enrolled");
   console.log("cellular ",data);
   
    return (
    
            <Container>   
              <ul className="mt-4" style={{"listStyle": "none"}}>
            <li >
              <div><strong>Owner: </strong>{data[0].UserEmailAddress}</div>
              <div><strong>OG: </strong>{data[0].LocationGroupName}</div>
              <div><strong>Model: </strong>{data[0].Model}</div>
              <div><strong>OS: </strong>{data[0].Platform} {data[0].OperatingSystem}</div>
              <div><strong>Last seen: </strong>{dateFormat(data[0].LastSeen, "mediumDate")}</div>
              <div><strong>Security Patch: </strong>{dateFormat(data[0].SecurityPatchDate, "mediumDate")}</div>
              <div><strong>Enrollment Status: </strong>{data[0].EnrollmentStatus}</div>
              <div><strong>Compliance Status: </strong>{data[0].ComplianceStatus}</div>
              <div><strong>Serial Number: </strong>{data[0].SerialNumber}</div>
              <div><strong>Mac Address: </strong>{data[0].MacAddress}</div>
              <div><strong>Imei: </strong>{data[0].Imei}</div>
              <div><strong>Udid: </strong>{data[0].Udid}</div>
              <div><strong>Uuid: </strong>{data[0].Uuid}</div>
              <div><strong>Battery: </strong>{data[0].BatteryLevel}</div>
              {  (data[0].Platform==="Android")?<div><strong>Disk usage Android: </strong>{((data[0].AvailablePhysicalMemory/data[0].TotalPhysicalMemory)*100).toFixed(0)}% available</div>:''}
    {  (data[0].Platform==="Apple")?<div><strong>Disk usage Ios: </strong>{((data[0].AvailableDeviceCapacity/data[0].DeviceCapacity)*100).toFixed(0)}% available</div>:'' }
    {  (data[0].Platform==="Android")? (
       <React.Fragment>
    <div><strong>Current Operator: </strong>{data[1].CellularNetworkInfo.CurrentOperator}</div>
              <div><strong>Carrier Network: </strong>{data[1].CellularNetworkInfo.SubscriberCarrierNetwork}</div>
    <div><strong>Roaming: </strong>{(data[1].RoamingStatus.toString())}</div>
    </React.Fragment>) :''}
              <div><strong>Is compromised? </strong>{(data[3].IsCompromised.toString())}</div>
              <div><strong>Passcode: </strong>{(data[3].IsPasscodePresent.toString())}</div>
              <div><strong>Is Passcode compliant? </strong>{(data[3].IsPasscodeCompliant.toString())}</div>
              
              </li>
            </ul>
           
            </Container>
        
    );

    }
  }


export default DeviceInfo;
