export const fetchIntelliData = async (email, sub, dispatch) => {
  const url = `${SERVER_URL}/credentials`;

    const data = {
        email,
        sub
    }
     
  const creds = await axios.get(url,{params:data}); 
    
    if (creds.data[0]){
       

        const dbcreds = {clientid: creds.data[0].clientid,
          url: creds.data[0].url,
          secret: creds.data[0].secret}
        
          dispatch(dbcreds);
        
      }
      else {
        
        }
    }

    
