import React, {useState, useEffect} from "react";
import {Container, Row, Col, Form, FormGroup, Input, Button, Label, Spinner} from 'reactstrap';
import { useAuth0 } from "../../Auth0/react-auth0-spa";
import axios from 'axios';
import base64 from 'react-native-base64';
import {fetchIntelliData, testCreds} from '../../apiCall/intelli';
import _ from 'lodash';
import {SERVER_URL} from '../../config';

const Intelligence = (props) =>  {

    const { user } = useAuth0();

    

    const [state, setState] = useState({
      email: user.email,
      sub: user.sub,
      loading: false,
      disabled2: true
        
    })

   // const [trigger, setTrigger] = useState(0)
    
    const [isOpen, setOpen] = useState(true)
  const toggleEdit= () => setOpen(!isOpen)

  const [isDisabled, setDisabled] = useState(false);
  const toggleDisabled = () => setDisabled(!isDisabled);


     useEffect( () => {
        // code to run on component mount
      console.log("secret ", props.secret);
       
      
      
      
      fetchIntelliData(user.email, user.sub, props.retrieveCreds );
    /*   props.updateCreds ({url: state.url,
          clientid: state.clientid,
          secret: state.secret}) */
      }, [])

    
    
      const handleChange = (evt) => {
   
        props.selectCreds(
         
          evt.target.name,evt.target.value
          )

          setState({
            ...state,
            disabled2: true,
            message: "" ,
            loading: false   
       })

  
      }

      const testCred = async () =>{
        const encoded = base64.encode(`${props.creds.clientid}:${props.creds.secret}`)
        console.log("region2 ",props.creds.url.split('.',2)[1]);
        const region = props.creds.url.split('.',2)[1]; 
        setState({
          ...state,
          message:'',
        loading: true});
        await testCreds( region, encoded).then(
          jwt => {  console.log(jwt.data);
           if (_.isEmpty(jwt.data) || jwt.data.toString().startsWith('mafautestgrande') ){
              console.log("PAS BOMMMMMMMM");
            
              setState({
                ...state,
                message: "Check your credentails, test failed",
                disabled2: true,
                color: 'red',
                loading: false
              }) 
            }
          else{ 
             
              setState({
              ...state,
             
              disabled2: false,
              color: 'green',
            message:'Test succesfull',
            loading: false
               })
             }  
           }
        )
      }

      const saveCreds = async () => {

    
        const url = `${SERVER_URL}/credentials`;
        const encoded = base64.encode(`${props.creds.clientid}:${props.creds.secret}`)
        console.log("region2 ",props.creds.url.split('.',2)[1]);
        const region = props.creds.url.split('.',2)[1]; 
        const data ={
          email: user.email,
          sub: user.sub,
          url: props.creds.url,
          clientid: props.creds.clientid,
          secret: props.creds.secret,
          encoded,
          region 
        }
        
      try { const jwt = await axios.post(url, data); 
        console.log("jwtStatus ",jwt.status);
        
        if (jwt.status === 200){
            console.log("toto");

                 toggleEdit();
                 toggleDisabled(); 
            console.log("state 2",isOpen);
           
           
           
              //save in redux props
              props.retrieveCreds({url: props.creds.url,
                clientid: props.creds.clientid,
                secret: props.creds.secret});


              return jwt;
          }
          else {
            
            }

      
      } catch(err){
       console.log(err);
        
        }
        
    }

      const onSubmit =  (e) => {
        e.preventDefault();
        e.stopPropagation();  
        saveCreds();
        setState({...state,
          disabled2: true,
        message:'' })
        
        
        console.log("state ",state);
       
    }

    
    const Test = () => {return <Row><Col sm ="4"><Button type="button" color="primary" block size="sm" onClick = {testCred}>Test</Button></Col><Col sm ="4"><Button type="submit" value="save" block disabled={state.disabled2} className="btn-sm  btn-info">Submit</Button></Col></Row>}



    return (
      
        <Container >
           <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
           <Col sm="8" className="text-center">
      <h5 className="mb-3 title" style={{textAlign: "center"}}>API credentials from your service account</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
        <Form onSubmit={onSubmit} > 
        <Col sm="8">
        <FormGroup>
            <Label for="url"> Token Endpoint:  </Label>
                <Input type="text" className="form-control" name="url" value={props.creds.url} onChange={handleChange} required disabled={!isDisabled}/>
                </ FormGroup >
       <FormGroup>
            <Label for="clientid">Client Id:  </Label>
                <Input type="text" className="form-control" name ="clientid" value={props.creds.clientid} onChange={handleChange} required disabled={!isDisabled}/>
                </ FormGroup >
            <FormGroup>
            <Label for="secret">Client Secret: </Label>
                <Input type="password" className="form-control" name ="secret" value={props.creds.secret} onChange={handleChange} required disabled={!isDisabled}/>
                </FormGroup >
              <FormGroup>
                {(!isOpen)?<Test/>:
                 <Row>
                 <Button color="primary" size="sm" block active  onClick = {()=>{toggleDisabled(); toggleEdit()}}>Edit</Button>
                 </Row>}
              </FormGroup >
                </Col>
       </Form >
      
       <Row>
             <Col sm="2"></Col>
             {state.loading ? <Spinner  animation="border" variant="warning" /> : "" }
             <Col sm="auto"> <h2 style ={{color: state.color}}>{state.message}</h2></Col>
           </Row>
       
       </Container >
   
    
    
   
    )
 }
  


export default Intelligence;

