function initFontAwesome() {
  library.add(faLink);
  library.add(faUser);
  library.add(faHome);
  library.add(faPowerOff);
  library.add(faCog);
  library.add(faLightbulb);
  library.add(faMobileAlt);
  library.add(faDesktop);
  library.add(faCheck);
}

export default initFontAwesome;
