import React from 'react';
import {Table, Button} from 'reactstrap';
import {startTask, deleteTask, cancelTask} from '../../apiCall/agenda';
import { useAuth0 } from "../../Auth0/react-auth0-spa";

 const Tasks =(props) => {


  const { getTokenSilently } = useAuth0();
  
  const callCancelTask = async (name, index0) => {
    try {
      const token = await getTokenSilently();
      cancelTask(name, token).then((data)=>{
        console.log("datafrom front stop ", data)
      props.onButton(index0);
      })
    }catch (error) {
      console.error(error);
    }
  };

  const callStartTask = async (index0, email, sub, name, interval) => {
    try {
      const token = await getTokenSilently();
      startTask(email, sub, name, interval, token).then((data)=>{
        console.log("datafrom front start ", data)
      props.onButton(index0);
      })
    }catch (error) {
      console.error(error);
    }
  };

  const callDeleteTask = async (name) => {
    try {
      const token = await getTokenSilently();
      deleteTask(name, token).then((data)=>{
        console.log("datafrom front stop ", data)
      
      })
    }catch (error) {
      console.error(error);
    }
  };
  
    return (
      <div>
        
        <Table  striped borderless className="mt-5"> 
          <thead>
            <tr className='cron-builder-bg'>
              <th>#</th>
              <th  width="50%">Scheduled</th>
              <th width="25%">Repository</th>
              <th width="25%">Commands</th>
            </tr>
          </thead>
          <tbody>
            
            {props.items.map((item, i )=> {
         //     console.log("from map ", i)
              const index =i;
              return (
                <tr key={i}>
                  <th scope='row'>{i+1}</th>
                  <td>{item.scheduled}</td>
                  <td align="center">{item.repo}</td>
                  <td align="center"><Button  className="mr-4" color={item.position?"danger":"success"}  onClick= {item.position?() =>{
                   console.log("stop and position",item.position )
                   

                   callCancelTask(item.name, index)
                    
                    }: () => {
                      
                      console.log("startand position",item.position );
                      callStartTask(index, props.email, props.sub, item.name, item.interval)
                      
                }}>{item.position?"Stop":"Start"}</Button> 
                  
                  
                    {!item.position? <Button color="danger"  onClick= {()=>{
                         console.log("begining of i", index)
                         const itemrem = props.items;
                         itemrem.splice(index,1);

                         callDeleteTask(item.name)
                         
                         props.delitem(itemrem);
                         }
                         }>Delete</Button>:"" }</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    );
  }

export default Tasks;