import React, { useState } from "react";
import { NavLink as RouterNavLink } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from 'classnames';
import SideBar from "../Sidebar/SideBar";
import Content from '../content/Content';
import './navbar.css';
import Dasboard from '../Dashboard'


import {
  Collapse,
  Container,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  Button,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Row,
  Col
} from "reactstrap";

import { useAuth0 } from "../../Auth0/react-auth0-spa";

const NavBar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { user, isAuthenticated, loginWithRedirect, logout } = useAuth0();
  const toggle = () => setIsOpen(!isOpen);

  const [state, setState] = useState({
    color1:'primary',
    color2:'info'
      
  })

  const logoutWithRedirect = () =>
    logout({
      returnTo: window.location.origin
    });

    //const items = {...localStorage};
    const [menu, setMenu]= useState(1);

   


  return (
    <div className="nav-container">
      <Navbar color="light" light expand="md" className=" " >
        <Container>
          <NavbarBrand className="logo" />
       
          <NavbarToggler onClick={toggle} />
          <Collapse isOpen={isOpen} navbar>
            <Nav className="mr-auto" navbar>
            
              {isAuthenticated && (
                <React.Fragment>
              <NavItem>
                  <Button
                  color={state.color1}
                   className="mx-5 " 
                   onClick={() => {setMenu(1);
                    setState({
                      color1:'primary',
                     color2:'info'
                    })
                    } 
                  }
                    >
                      <FontAwesomeIcon icon="mobile-alt" className="mr-3" />
                     
                     UEM
                     
                  </Button>
                </NavItem>
                
                 <NavItem>
                 <Button
                color={state.color2}
                  className="mx-5" 
                 
                  onClick={() => {setMenu(2);
                    setState({
                      color1:'info',
                     color2:'primary'
                    })
                    } 
                  }
                   >
                     <FontAwesomeIcon icon="lightbulb" className="mr-3" />
                    INTELLIGENCE
                    
                 </Button>
               </NavItem>
               </React.Fragment>
              )}




              

            </Nav>
            <Nav className="d-none d-md-block" navbar>
              {!isAuthenticated && (
                <NavItem>
                  <Button
                    id="qsLoginBtn"
                    color="primary"
                    className="btn-margin"
                    onClick={() => {loginWithRedirect({});
                  }}
                  >
                    Log in
                  </Button>
                </NavItem>
              )}
              {isAuthenticated && (
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret id="profileDropDown">
                    <img
                      src={user.picture}
                      alt="Profile"
                      className="nav-user-profile rounded-circle"
                      width="50"
                    />
                  </DropdownToggle>
                  <DropdownMenu>
                    <DropdownItem header>{user.name}</DropdownItem>



                    
                    
                    <DropdownItem
                      id="qsLogoutBtn"
                      onClick={() => logoutWithRedirect()}
                    >
                      <FontAwesomeIcon icon="power-off" className="mr-3" /> Log
                      out
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              )}
            </Nav>
            {!isAuthenticated && (
              <Nav className="d-md-none" navbar>
                <NavItem>
                  <Button
                    id="qsLoginBtn"
                    color="primary"
                    block
                    onClick={() => loginWithRedirect({})}
                  >
                    Log in
                  </Button>
                </NavItem>
              </Nav>
            )}
            {isAuthenticated && (
              <Nav
                className="d-md-none justify-content-between"
                navbar
                style={{ minHeight: 170 }}
              >
                <NavItem>
                  <span className="user-info">
                    <img
                      src={user.picture}
                      alt="Profile"
                      className="nav-user-profile d-inline-block rounded-circle mr-3"
                      width="50"
                    />
                    <h6 className="d-inline-block">{user.name}</h6>
                  </span>
                </NavItem>
               
                <NavItem>

                  <FontAwesomeIcon icon="power-off" className="mr-3" />
                  <RouterNavLink
                    to="#"
                    id="qsLogoutBtn"
                    onClick={() => { 
                      logoutWithRedirect()}}
                  >
                    Log out
                  </RouterNavLink>
                </NavItem>
              </Nav>
            )}
          </Collapse>
        </Container>
      </Navbar>
      {isAuthenticated && (   <Row>
         <Col sm="3" className="pr-0">
      <SideBar toggle={toggle} isOpen={!isOpen} menu={menu}/>
      </Col>
      <Col sm="8" offset-sm-1="true" className="pl-0">
    
    <Container fluid className={classNames('content', {'is-open': isOpen})}>
     
     <Content email={user.email} sub={user.sub} name={user.name}/>
    </Container>
    </Col>
    </Row> )}
    {!isAuthenticated && (   
         <Dasboard />
          )}

    </div>
  );
};

export default NavBar;
