import React, { Component } from 'react';
import { Card, Button ,Form, FormGroup, CustomInput} from 'reactstrap';
import Weekly from './Weekly';
import Tasks from './Tasks';
import cronstrue from 'cronstrue';
import axios from "axios";
import {fetchTasks} from '../../apiCall/intelli';
import {createJob} from '../../apiCall/agenda';
import uniqid from 'uniqid';
import {fetchData} from '../../apiCall/midatalakes';
import {fetchIntelliData} from '../../apiCall/intelli';
import { fetchGoogleData} from '../../apiCall/google';
import {SERVER_URL} from '../../config';



class Cron extends Component {

 state  = {repo:'',
           value: ['0', '0', '*',  '*',  '*'],
          interval:"0 0 * * *",
           gen:'' ,
           message:''
      }

   

      componentDidMount(){      
          fetchTasks(this.props.email, this.props.sub, this.props.retrieveTasks );
          fetchGoogleData(this.props.email, this.props.sub, this.props.gooRetrieveCreds );
          fetchData(this.props.email, this.props.sub, this.props.miretrieveCreds );
          fetchIntelliData(this.props.email, this.props.sub, this.props.retrieveCreds );
        fetchData(this.props.email, this.props.sub, this.props.miretrieve2Creds);
      }

      onValueChange=(val)=> {
        if (val && val.length) {
          this.setState({
            value: val
          });
        } else {
          this.setState({
            value: ['0', '0', '*',  '*',  '*']
          });
          val = [ '0', '0', '*', '*',  '*'];
        }
        console.log("val from cron ", val)
       this.parentChange(val);
      }

      parentChange=(val)=> {
        var newVal = '';
      
      if (val[4] === '*') {newVal = '*'}
      else{
      var inorder = val[4].split("!").map(Number).sort().toString().replace(/,/g, '!');
       val.splice(4, 1, inorder);
        
        newVal = val.toString().replace(/,/g, ' ');
        newVal = newVal.replace(/!/g, ',');
      }
       console.log("newVal ", newVal)
        this.setState({interval:newVal})
      }

      

      getVal=() =>{

     if(this.state.interval !== '*'){
         var val = cronstrue.toString(this.state.interval);
         
         if (val.search('undefined') === -1) {
           return val;
         }
   
         return '-';
        }    
       }

      handleChange = event => {
       
        this.setState({
          repo: event.target.value,
          gen: event.target.id,
          unique: 0,
          message:''
         
        });
      }
      


      resetMessage = () => {
        this.setState({
          
          message: ''
        
        });
      }

      onSubmit = async (e) => {
        e.preventDefault();
      }

      handleTaskSubmit = (e) => {
        e.preventDefault();
        if((this.props.creds.clientid && this.props.micreds.azureclientid) ||(this.props.creds.clientid && this.props.mi2creds.azureconnectstring) ||(this.props.creds.clientid && this.props.goocreds.bucket)){
         
       
        let items = [...this.props.tasks.items] //[...this.state.items];
       
        if (this.state.value[4]==='*') {
         
          this.setState({message: "please check at least one checkbox"});
        return;} 
      
        if (!this.state.repo){this.setState({message: "please select one datalake or google storage"}); return;}
         
        if (items.length > 2) {this.setState({message: "You reached maximum number of Jobs"})}
        else{
        const uniq = uniqid();
        const name = this.props.sub+"-"+uniq;
       
      
       
        items.push({scheduled: this.getVal(), repo: this.state.repo, position: this.props.tasks.position, name, interval:this.state.interval})
         console.log("items from submit: ", items)
   
          this.saveTasks(items);
          const urlapi= `http://127.0.0.1:8000/api/agenda/${this.state.gen}`;
         
         
         
         createJob(name, urlapi);
        }
      }
        else {  this.setState({message: "please go to settings and configure Intelligence and Cloud storage"});
      }
       }

       
  

      onButtonPosition =(index) => {
        let items = [...this.props.tasks.items] 

      const newItem=  items.map((item, i)=>{ if( i === index)
         { 
          item.position = !item.position ; }
       
      return item}) 
      
     
      this.saveTasks(newItem); 
      }


    onItemDel =(newtem) =>{
   this.setState({message: ''})
   this.saveTasks(newtem);
    }

    
     saveTasks = async (tasks) => {

    
      const url = `${SERVER_URL}/task`;
      
      const data ={
        email: this.props.email,
        sub: this.props.sub,
        items: tasks
       
      }
      
    try { const result = await axios.post(url, data); 
    //  console.log("jwtStatus ",result);
      
     if (result.status === 200){
         

            //save in redux props
        //    console.log("from cron retrived ",  )
            this.props.retrieveTasks({items: tasks});


            return result;
        }
        else {
          
          } 

    
    } catch(err){
     console.log(err);
      
      }
      
  }
    

      render() {

       console.log("valeu from cron", this.props)
       return (
         <div>
      <Card style={{width:"70%"}}>
     <Form >
      <Weekly value={this.state.value}
      onChange={this.onValueChange}  resetMessage ={this.resetMessage} />
    
    <div className= "cron-builder-bg" >{this.getVal()} </div>
   { (this.props.showResultCron) ?   <div className= "cron-builder-bg"  >{this.state.value.toString().replace(/,/g, ' ').replace(/!/g, ',')}</div> :"" }
   
   <FormGroup check inline className='text_align_lake'>
        
    {(this.props.micreds.azureclientid)  ?   <CustomInput id="genv1" type="radio"  name="gen" value="Datalake Gen v1" checked={this.state.repo === "Datalake Gen v1"} label="Datalake Gen v1" className="pr-2" onChange={this.handleChange}/> :''}
        
      
    {(this.props.mi2creds.azureconnectstring)  ?   <CustomInput className="ml-3" id="genv2" type="radio" name="gen" value="Datalake Gen v2" checked={this.state.repo === "Datalake Gen v2"} label="Datalake Gen v2" onChange={this.handleChange}/>  :''}
       
    {(this.props.goocreds.bucket)  ?   <CustomInput className="ml-3" id="genv3" type="radio" name="gen" value="Google storage" checked={this.state.repo === "Google storage"} label="Google storage" onChange={this.handleChange}/>  :''}
    

      </FormGroup>
   
   <Button  variant="outline-dark" size="sm" block onClick={this.handleTaskSubmit}>Submit</Button>
   
   </Form>
   <span className='cron-message-bg'>{this.state.message}</span>
    </Card>
    <Tasks  items={ this.props.tasks.items/*this.state.items*/} delitem={this.onItemDel} onButton={this.onButtonPosition} email={this.props.email} sub={this.props.sub}   />
    </div>
    )
      }
}

export default Cron