


import * as ActionTypes from './ActionTypes';

export function selectIntelliCreds(intelliname, intellivalue){
    
    return {
        type : ActionTypes.INTELLI_SELECTED,
        payload :  {[intelliname]: intellivalue}
    }
}


export function retrieveIntelliCreds(intellicreds){
   
    return {
        type : ActionTypes.INTELLI_RETRIEVED,
        payload : intellicreds
    }
}


