import React, { Component} from 'react';
import {Container, Row, Col, Form, FormGroup, Input, Spinner, CustomInput, Badge} from 'reactstrap';
import axios from 'axios';
import {SERVER_URL} from '../../../config';


class SingleApp extends Component {
  
  state ={
    apps: [],
    appid: '-1',
    action: '-1',
    type:'-1',
    loading: false,
    smartgroup: false,
    disabled3: false,
    color: "green"
  };

  


actionApp = async (email, sub,  appid, type, action, DeviceId)=>{
  const data ={
    email,
    sub,
    appid,
    type,
    action,
    DeviceId
  }
  const url = `${SERVER_URL}/uem/actionapp`
  try {

    console.log("request apps");
  const act = await axios.post(url,data)
    
  this.setState({message: act.data,
      loading: false})
      console.log("from action remover or install", act)
    if(act.data.includes('assigned')) this.setState({smartgroup:true,
    color: 'red'})
    
   // else this.setState({message: "Command has been sent"})

      return act.data; 


 
  }catch(err) { return err}

}



onSubmit = (e) => {
  e.preventDefault();
  this.setState({ loading: true, smartgroup: false
    }, async () => {
 this.actionApp(this.props.email, this.props.sub, this.state.appid, this.state.type, this.state.action, this.props.deviceData.data[0].Id.Value);
 
  })
};


searchApp = async (email, sub,  type, platform, lgid) =>{
  const data ={
    email,
    sub,
    type,
    platform,
    lgid
  }
  const url = `${SERVER_URL}/uem/searchapps`
  try {

    console.log("request apps");
  const apps = await axios.post(url,data)
    
  this.setState({apps: apps.data,
      loading: false})
      console.log("from funciotn  in then", apps.data)
      return apps.data;


 
  }catch(err) { return err}

}

createSm = async (email, sub,  DeviceId, gid, appid, type) =>{
  
  const data ={
    email,
    sub,
    DeviceId,
    gid,
    appid,
    type
  }
  const url = `${SERVER_URL}/uem/assignsmapp`;
  try {
    await axios.post(url,data)
    this.setState({disabled3:true,
      message: "Smart Group created, you can install app",
      color: 'green',
      loading: false})
     
  }catch(err) { return err}

}

handleSm = (e) => {
  if(e.target.checked) {
    
    this.setState({ loading: true
      }, async () => {
   await this.createSm(this.props.email, this.props.sub, this.props.deviceData.data[0].Id.Value,this.props.deviceData.data[0].LocationGroupId.Id.Value, this.state.appid, this.state.type);
   
    })

  }

};
     
handleChangeAction = (evt) => {
  const {value} = evt.target;
  if (value !== '-1'){
  
  this.setState({action: value,
  message:'',
  smartgroup: false,
  disabled3: false})
  //this.toggleDisabled();
  } else this.setState({action: '-1',
  message:'',
  smartgroup: false,
  disabled3: false})
};

handleChangeType = (evt) => {
  const {value} = evt.target;
console.log(this.state.action);
  console.log(value)

  if (value !== '-1'){
   console.log("platfrom ", this.props.deviceData.data[0].Platform) 
  //this.setState({type: value})
  this.setState({ loading: true,
    type: value,
    message:'',
    smartgroup: false,
    disabled3: false}, async () => {
 await this.searchApp(this.props.email, this.props.sub, value, this.props.deviceData.data[0].Platform, this.props.awcreds.lgid);
 
  })
 
  //this.toggleDisabled();
  }else this.setState({type: '-1',
  message:'',
  smartgroup: false,
  disabled3: false,})
};

handleChangeApp= (evt) => {
  const {value} = evt.target;
  console.log("app id1 ", value)
  if (value !== '-1'){
  
  this.setState({appid: value,
    message:'',
    smartgroup: false,
    disabled3: false})
  
  } else this.setState({appid: '-1',
  message:'',
  smartgroup: false,
  disabled3: false})
};
  
 render() {
 
  const apps = this.state.apps;
  
  console.log("apss ???? ", this.props.deviceData.data[0].Platform)
  const appsArray = this.props.deviceData.data[2].DeviceApps.filter(item => item.IsManaged);
  const appsEvenArray = appsArray.filter((product, index) => index % 2 )
  const appsOddArray = appsArray.filter((product, index) => !(index % 2) )
  
  console.log("test apps", appsArray);
  

    return (
      <Container >
    
     
  <Form onSubmit={this.onSubmit} style={{ "marginTop": "20px" }}> 
  <Row form>
  <Col md={3}>
  <FormGroup>  
  <Input type ="select" className="form-control" onChange={this.handleChangeAction} >
    <option name="action" value='-1'>Select Action</option>
    <option name="action" value='install'>Install App</option>
    <option name="action" value='uninstall'>Remove App</option>
  </Input>
  </FormGroup>
          </Col>
          <Col md={3}>
  <FormGroup>  
  <Input type ="select" className="form-control" onChange={this.handleChangeType} >
    <option name="type" value='-1'>Select App Type</option>
    <option name="type" value='internal'>Internal</option>
    {(this.props.deviceData.data[0].Platform !=="AppleOsX")?<option name="type" value='public'>Public</option>:''}
  </Input>
  </FormGroup>
          </Col>



          <Col md={3}>
          <FormGroup>
        
        <Input type ="select" className="form-control" onChange={this.handleChangeApp}  >
          <option value='-1'>select App</option>
    {(this.state.type === 'internal')?apps.map((v, i) => <option key={i} value={v.Id.Value}  name={v.BundleId}>{`${v.ApplicationName} ${v.AppVersion}`}</option>):
    apps.map((v, i) => <option key={i} value={v.Id.Value}  name={v.BundleId}>{`${v.ApplicationName}`}</option>) }
    
        </Input>
        
        
        </FormGroup >
        </Col>
        <Col md={2}>
          <FormGroup>
        
       
        
         
          <Input type="submit" value="Submit" id="awbut" name="awbut" className="btn-sm btn-block btn-info"  disabled={(this.state.action === '-1' || this.state.type === '-1' || this.state.appid === '-1')?true:false} />
         

        </FormGroup >
        </Col>
        </Row>   
 </Form >
 
     <Row>
    <h2 className="mx-auto"style={{color:this.state.color}}>{this.state.message}</h2>
     </Row>
    
     <Row>
          <Col md={5}></Col>
          <Col>
           {this.state.loading ? <Spinner  animation="border" variant="warning" /> : "" }
           </Col>
           </Row>
      <Row>
          {(this.state.smartgroup)? <Form  > 
           <span><Badge color="secondary" >Check the box to create Smart Group assignment</Badge></span>
           <CustomInput type ="checkbox" id= "smartgroup" disabled={this.state.disabled3} onChange={this.handleSm} label={`Dummy-sm-${this.props.deviceData.data[0].Id.Value}`} />
            </Form>:''}
            </Row>
            <h6 className="mt-4">List of managed apps installed</h6>
            <Row>
            <Col md={6} className="m-0 ">
              <ul className="singleul">
            {appsEvenArray.map(item => {
              return <li className="singleli hr" key ={item.ApplicationIdentifier}>
                <div><strong>{item.ApplicationName}</strong></div>
             <div>{item.ApplicationIdentifier} </div>
             <div><Badge color="secondary" >{item.Type}</Badge> </div>
               </li>
            })}
            </ul>
            </Col>
            <Col md={6} className="m-0 ">
            <ul className="singleul">
            {appsOddArray.map(item => {
             return <li className="singleli hr" key ={item.ApplicationIdentifier} mb={1}>
             <div><strong>{item.ApplicationName}</strong></div>
             <div>{item.ApplicationIdentifier} </div>
             <div><Badge color="secondary" >{item.Type}</Badge> </div>
            </li>
           })} 
            </ul>
            </Col>
            </Row>
 </Container >
    );

    }
  }


export default SingleApp;
