import React, {useState, useEffect} from "react";
import {Container, Row, Col, Form, FormGroup, Input, Button, Label, Spinner, CustomInput} from 'reactstrap';
import { useAuth0 } from "../../Auth0/react-auth0-spa";
import axios from 'axios';
import {fetchGoogleData, testCreds} from '../../apiCall/google';
import _ from 'lodash';
import {SERVER_URL} from '../../config';

const Google = (props) =>  {

    const { user, getTokenSilently  } = useAuth0();
    
    

    const [state, setState] = useState({
      email: user.email,
      sub: user.sub,
      loading: false,
      disabled2: true,
      disabled3: true,
      fileName:'',
      invalidFile: false,
      messupload:''  
        
    })

   // const [trigger, setTrigger] = useState(0)
    
    const [isOpen, setOpen] = useState(true)
  const toggleEdit= () => setOpen(!isOpen)

  const [isDisabled, setDisabled] = useState(false);
  const toggleDisabled = () => setDisabled(!isDisabled);


     useEffect( () => {
        // code to run on component mount
     
       
      
      
      
      fetchGoogleData(user.email, user.sub, props.retrieveCreds );
   
      }, [])

     const handleFileChange = async ({target: {files}}) =>{
          console.log("target ", {target: {files}});
          console.log("files", files);
          props.selectCreds('fileName','')
          setState({ ...state,  invalidFile: false,  messupload:''  });
        const cancel = !files.length;
        if (cancel) {console.log("retrun from upload"); return;}
    
        const [{ size, name, type }] = files;
        const maxSize = 5000;
        console.log("mime ", type)
    
        if (size < maxSize && type === 'application/json') {
          const url = `${SERVER_URL}/upload/google`;
          const token = await getTokenSilently();

          const headers = {
            Authorization: `Bearer ${token}`
        }

        console.log("token ", token);
        props.selectCreds('fileName',name)
          setState({ ...state, invalidFile: false, message: '' });
          const formData = new FormData();
        formData.append('sampleFile', files[0]);
        formData.append('sub', user.sub);
        axios.post(url, formData, {headers}).then(res => {
              if(res) setState({ ...state, disabled3: false, invalidFile: false, messupload:''});
            console.log(res)
        })
          
        } else {
          setState({ ...state, fileName: '', invalidFile: true,  message: '', messupload:'check FAQ and upload correct google json file', disabled3: true  });
          console.log("invalid ", size)
        }
      } 

      

    
    
      const handleChange = (evt) => {
   
       props.selectCreds(
         
          evt.target.name,evt.target.value
          )

          setState({
            ...state,
            disabled2: true,
            message: "" ,
            loading: false   
       }) 
       
      }

      const testCred = async () =>{
        
        const bucket = props.goocreds.bucket; 
        if (bucket){
        setState({
          ...state,
          message:'',
        loading: true});
        await testCreds( bucket, state.sub).then(
          jwt => {  console.log("this is from test ",jwt.data);
                  
           if (_.isEmpty(jwt.data)  ){
              console.log("PAS BOMMMMMMMM", jwt.data);
            
              setState({
                ...state,
                message: "Check your credentials, test failed",
                disabled2: true,
                color: 'red',
                loading: false
                
              }) 
            }
          else{ 
             
              setState({
              ...state,
              projectId: jwt.data,
              disabled2: false,
              color: 'green',
            message:'Test successfull',
            loading: false
               })
             }  
           }
        )
          }  else setState({
            ...state,
            message: "Bucket name is missing",
            
            color: 'red',
           
          }) 

      }

      const saveCreds = async () => {

    
        const url = `${SERVER_URL}/google`;
        
        const data ={
          email: user.email,
          sub: user.sub,
          bucket: props.goocreds.bucket,
          fileName: props.goocreds.fileName,
          projectId: state.projectId        
        }
        
      try { const jwt = await axios.post(url, data); 
        console.log("jwtStatus ",jwt.status);
        
        if (jwt.status === 200){
            console.log("toto");

                 toggleEdit();
                 toggleDisabled(); 
            console.log("state 2",isOpen);
           
           
           
              //save in redux props
              props.retrieveCreds({bucket: props.goocreds.bucket,
                fileName: props.goocreds.fileName
                });


              return jwt;
          }
          else {
            
            }

      
      } catch(err){
       console.log(err);
        
        }
        
    }

      const onSubmit =  (e) => {
        e.preventDefault();
        e.stopPropagation();  
        saveCreds();
        setState({...state,
          disabled2: true,
        message:'' })
        
        
        console.log("state ",state);
       
    }

    
    const Test = () => {return <Row ><Col sm ="4"><Button type="button" color="primary" block size="sm" onClick = {testCred} disabled={state.disabled3}>Test</Button></Col><Col sm ="4"><Button type="submit" value="save" block disabled={state.disabled2} className="btn-sm  btn-info">Submit</Button></Col></Row>}



    return (
      
        <Container >
           <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
           <Col sm="8" className="text-center">
      <h5 className="mb-3 title" style={{textAlign: "center"}}>Google storage credentials</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
        <Form onSubmit={onSubmit} > 
        <Col sm="8">
        <FormGroup>
<Label for="bucket"> Bucket name:  </Label>
    <Input type="text" className="form-control" id="bucket" name="bucket"  value={props.goocreds.bucket} onChange={handleChange}  
          disabled={!isDisabled} required/>
</ FormGroup >
<FormGroup >
<Label for="jsonfile"> Google credentials file:  </Label>
        <CustomInput
          type="file"
          id="jsonfile"
          className="form-control"
          name="customFile"
          label={props.goocreds.fileName || 'select json file'}
          onChange={handleFileChange}
          invalid={state.invalidFile}
          required 
          disabled={!isDisabled} 
          onClick={(event)=> { 
            event.target.value = null;
            setState({...state,
            messupload: '',
            message:'' })
            
       }}
          />
          <Col sm="auto" className="mb-1 mt-3"> <h2 style ={{color: state.color}}>{state.messupload}</h2></Col>
      </FormGroup>
            
              <FormGroup className="mt-4">
                {(!isOpen)?<Test/>:
                 <Row>
                 <Button color="primary" size="sm" block active  onClick = {()=>{toggleDisabled(); toggleEdit()}}>Edit</Button>
                 </Row>}
              </FormGroup >
                </Col>
       </Form >
      
       <Row>
             <Col sm="2"></Col>
             {state.loading ? <Spinner  animation="border" variant="warning" /> : "" }
             <Col sm="auto"> <h2 style ={{color: state.color}}>{state.message}</h2></Col>
           </Row>
       
       </Container >
   
    
    
   
    )
 }
  


export default Google;

