
import axios from 'axios';
import {SERVER_URL} from '../config';


export const fetchAwData = async (email, sub, dispatch) => {
  const url = `${SERVER_URL}/airwatch`;

    const data = {
        email,
        sub
    }
     
  const creds = await axios.get(url,{params:data}); 
    console.log("aw ", creds.data[0])
    if (creds.data[0]){
       

        const dbcreds = {apikey: creds.data[0].apikey,
          awurl: creds.data[0].awurl,
          awadmin: creds.data[0].awadmin,
          awpassword: creds.data[0].awpassword,
          lgid: creds.data[0].lgid
        }
        console.log("dbcreds ", dbcreds);
          dispatch(dbcreds);
        
      }
      else {
        
        }
    }

    export const testCreds = async (awurl, apikey, awencoded, username) => {
      const url = `${SERVER_URL}/uem/testcreds`;

      console.log(url);
  
      const data = {
          awurl,
          apikey,
          awencoded,
          username
      }

      const admins = await axios.post(url,data); 
    
      return admins;
    } 

