import axios from 'axios';
import {SERVER_URL} from '../config';



export const createJob = async (name, urlapi) => {
  
  const url = `${SERVER_URL}/agenda/job`;

  const data = {
      name,
      urlapi   
  }
  

  const job = await axios.post(url,{data}); 

  return job;
}


export const deleteTask = async(name, token)=>{

  
  console.log("token from cratejob", token)

  const url = `${SERVER_URL}/agenda/job/delete`;

  const data = {
      name
  }

  const headers = {
    Authorization: `Bearer ${token}`
}

  const job = await axios.post(url,{data},{headers}); 
  console.log("this is a jo from deleteb");
  return job;
}

