
import axios from 'axios';
import {SERVER_URL} from '../config';


export const fetchGoogleData = async (email, sub, dispatch) => {
  const url = `${SERVER_URL}/google`;

    const data = {
        email,
        sub
    }
     
  const creds = await axios.get(url,{params:data}); 
  console.log("aw creds", creds)
    console.log("aw ", creds.data[0])
    if (creds.data[0]){
       

        const dbcreds = {
          bucket: creds.data[0].bucket,
          fileName: creds.data[0].fileName
        }
        console.log("dbcreds ", dbcreds);
          dispatch(dbcreds);
        
      }
      else {
        
        }
    }

    export const testCreds = async (bucket, sub) => {
      const url = `${SERVER_URL}/googlecloud/testcreds`;

      console.log(url);
  
      const data = {
          bucket,
          sub
      }

      const buckets = await axios.post(url,data); 
    
      return buckets;
    } 

    

    