

import { combineReducers } from 'redux';

import IntelliCreds from './intelli_creds';
import Lake1Creds from './lake1_creds';
import Lake2Creds from './lake2_creds';
import tasks from './task_reducer';
import AwCreds from './aw_creds';
import goocreds from './google_creds';

const rootReducer = combineReducers({
  icreds: IntelliCreds,
  lake1creds: Lake1Creds,
  lake2creds: Lake2Creds,
  tasks: tasks,
  awcreds: AwCreds,
  goocreds: goocreds
 
})

export default rootReducer;