

import * as ActionTypes from '../actions/ActionTypes';
import {intelliCreds} from './forms';

export default function (state = intelliCreds, action){
    switch(action.type){
        case ActionTypes.INTELLI_SELECTED:
          
            return Object.assign({},state, action.payload)

            case ActionTypes.INTELLI_RETRIEVED:
                    
                    return Object.assign({},state, action.payload)

        default:
            return state;
    }
    
}