import React from 'react';
import { Switch, withRouter} from 'react-router-dom';
import Intelligence from "../Settings/Intelligence";
import Airwatch from "../Settings/Airwatch";
import Datalakes from "../Settings/Datalakes";
import Google from "../Settings/Google";
import Report from "../Reports/Report";
import Transfer from "../Reports/ReportsTransfer";
import Transfer2 from "../Reports/ReportsTransferv2";
import TransferGoogle from "../Reports/ReportsTransGoogle";
import Scheduler from "../Reports/Scheduler";
import Contact from "../Contacts/Contact";
import PrivateRoute from "../PrivateRoute";
import FaqsList from "../Faqs/FaqsList";
import AwData from "../Airwatch/AwData";
import Admin from "../Airwatch/Admin";
import Licences from "../Airwatch/Licences";
import { connect } from 'react-redux';
import { selectIntelliCreds, retrieveIntelliCreds ,retrieveLake1Creds, selectLake1Creds, retrieveLake2Creds, selectLake2Creds, selectTasks, retrieveTasks, selectAwCreds, retrieveAwCreds, selectGoogleCreds, retrieveGoogleCreds} from '../redux/actions/ActionCreators';


const Content = props => {

  const email= props.email;
  const sub= props.sub;
  const name= props.name;


   
    return (
      <Switch>
       <PrivateRoute exact path="/" component={() => <FaqsList/> } />
        <PrivateRoute exact path="/about" component={() => "About" } />
        <PrivateRoute exact path="/Pages" component={() => "Pages" } />
        <PrivateRoute exact path="/faq" component={() => <FaqsList/> } />
        <PrivateRoute exact path="/contact" component={() => <Contact email={email} name={name}/> } />
        <PrivateRoute exact path="/datalakes" render={()=><Datalakes micreds={props.lake1credentials}  miselectCreds = {props.selectLake1Creds} miretrieveCreds= {props.retrieveLake1Creds } mi2creds={props.lake2credentials} miselect2Creds = {props.selectLake2Creds} miretrieve2Creds= {props.retrieveLake2Creds }/> }  /> 
        <PrivateRoute exact path="/intelli" render={()=><Intelligence creds={props.intellicredentials}  selectCreds = {props.selectIntelliCreds} retrieveCreds= {props.retrieveIntelliCreds}/> }  />
        <PrivateRoute exact path="/gcp" render={() => <Google goocreds={props.goocreds}  selectCreds = {props.selectGoogleCreds} retrieveCreds= {props.retrieveGoogleCreds}/> } />
        <PrivateRoute exact path="/reports" render={()=><Report creds={props.intellicredentials} email={email} sub={sub} retrieveCreds= {props.retrieveIntelliCreds}  /> } />
        <PrivateRoute exact path="/transfer" render={()=><Transfer micreds={props.lake1credentials} miretrieveCreds={props.retrieveLake1Creds} creds={props.intellicredentials} email={email} sub={sub} retrieveCreds= {props.retrieveCreds}  /> } />
        <PrivateRoute exact path="/transfer2" render={()=><Transfer2 mi2creds={props.lake2credentials} miretrieve2Creds= {props.retrieveLake2Creds } creds={props.intellicredentials} email={email} sub={sub} retrieveCreds= {props.retrieveCreds}  /> } />
        <PrivateRoute exact path="/transfergoogle" render={()=><TransferGoogle goocreds={props.goocreds} gooRetrieveCreds={props.retrieveGoogleCreds} creds={props.intellicredentials} email={email} sub={sub} retrieveCreds= {props.retrieveCreds}  /> } />
        <PrivateRoute exact path="/schedule" render={()=><Scheduler goocreds={props.goocreds} gooRetrieveCreds={props.retrieveGoogleCreds} creds={props.intellicredentials} retrieveCreds= {props.retrieveIntelliCreds} mi2creds={props.lake2credentials} miretrieve2Creds= {props.retrieveLake2Creds} micreds={props.lake1credentials} miretrieveCreds={props.retrieveLake1Creds} tasks={props.tasks} email={email} sub={sub}  selectTasks = {props.selectTasks} retrieveTasks= {props.retrieveTasks} /> } />
        <PrivateRoute exact path="/airwatch" render={()=><Airwatch awcreds={props.awcreds}  selectCreds = {props.selectAwCreds} retrieveCreds= {props.retrieveAwCreds}/> }  />  
        <PrivateRoute exact path="/awdevice" component={()=><AwData email={email} sub={sub} awcreds={props.awcreds}/>} />
        <PrivateRoute exact path="/admin" component={()=><Admin email={email} sub={sub} />} />
        <PrivateRoute exact path="/licences" component={()=><Licences email={email} sub={sub} />} />
                
      </Switch>
    )
    }

    const mapStateToProps = state => {
      return {
          intellicredentials: state.icreds,
          lake1credentials: state.lake1creds,
          lake2credentials: state.lake2creds,
          tasks: state.tasks,
          awcreds: state.awcreds,
          goocreds: state.goocreds
      };
  }; 

  const mapDispatchToProps = 
    {selectIntelliCreds,
     retrieveIntelliCreds,
     selectLake1Creds,
     retrieveLake1Creds, 
     selectLake2Creds,
     retrieveLake2Creds,
     selectTasks,
     retrieveTasks,
     selectAwCreds,
     retrieveAwCreds,
     selectGoogleCreds,
     retrieveGoogleCreds}

  
  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Content));  //Shorthand
