import React, { useState } from 'react';
import { Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap';
import DeviceInfo from './Tabs/DeviceInfo';
import SingleApp from './Tabs/SingleApp';
import SingleProfile from './Tabs/SingleProfile';
import { useAuth0 } from "../../Auth0/react-auth0-spa";

function DeviceTabs(props) {


  const { user } = useAuth0();


  



  const [activeTab, setActiveTab] = useState('1');
  return (
    <div>
      <Nav tabs>
        <NavItem>
          <NavLink className={activeTab == '1' ? 'active' : ''}  onClick={() => setActiveTab('1')}>
            Device Info
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink className={activeTab == '2' ? 'active' : ''} onClick={() => setActiveTab('2')}>
            Apps
          </NavLink>
          
        </NavItem>
        <NavItem>
          <NavLink className={activeTab == '3' ? 'active' : ''} onClick={() => setActiveTab('3')}>
           Profiles
          </NavLink>
          
        </NavItem>
        
      </Nav>
      <TabContent activeTab={activeTab}>
        <TabPane tabId="1"><DeviceInfo deviceData={props.deviceData}/></TabPane>
        <TabPane tabId="2"><SingleApp deviceData={props.deviceData} email={user.email} sub={user.sub} awcreds={props.awcreds}/></TabPane>
        <TabPane tabId="3"><SingleProfile deviceData={props.deviceData} email={user.email} sub={user.sub} awcreds={props.awcreds}/></TabPane>
      </TabContent>
    </div>
  );
}

export default DeviceTabs