
import React, { Component } from 'react';
import {  CardBody, CustomInput, Label, FormGroup, Input,CardTitle , Col, Row} from 'reactstrap';




class Weekly extends Component {



 state={
   value:[]
 }


 getHours = () =>{
  var hours = [];
  var leap = parseInt(this.props.hours) || 1;

  for (var i = 0; i < 24; i = i + leap) {
    hours.push(React.createElement("option", {
      key:i,
      id: i,
      value: i < 10 ? "0".concat(i) : i
    }, i < 10 ? "0".concat(i) : i));
  }

  return hours;
}

 getMinutes = () => {
  var minutes = [];
  var leap = parseInt(this.props.minutes) || 1;

  for (var i = 0; i < 60; i = i + leap) {
    minutes.push(React.createElement("option", {
      key:i,
      id: i,
      value: i < 10 ? "0".concat(i) : i
    }, i < 10 ? "0".concat(i) : i));
  }

  return minutes;
}

onAtHourChange =(e) => {
  var val = this.state.value;
  //val[0] = '0';
  val[1] = "".concat(e.target.value);
  this.props.onChange(val);
}

onAtMinuteChange=(e) =>{
  var val = this.state.value;
  //val[0] = '0';
  val[0] = "".concat(e.target.value);
  this.props.onChange(val);
}

onCheck= (e) => {
  var val = this.state.value;
  //val[0] = '0';

  if (e.target.checked) {
    val[2] = '*';
    val[3] = '*';
    console.log("target - ", e.target.value);

    if (val[4] === '*' ) {
      val[4] = e.target.value;
    } else {
      val[4] = val[4] + '!' + e.target.value;
      console.log("val[4] ", val[4])

    }
  } else {
    console.log("length - ", val[4].length);
    if (val[4].length > 1) {
      val[4] =  val[4].split("!") 
     
      val[4].splice(val[4].indexOf(e.target.value), 1);
     
      val[4] = val[4].toString().replace(/,/g, '!');
  
    } else {
      console.log("length - low - ", val[4].length);
      val[4] = '*';
    }
  }
  
  this.props.onChange(val);
  this.props.resetMessage();
}

  

  render(){
    
//check length if nothing working
    this.state.value = this.props.value ;
    
    return (
      
          
          <CardBody>
            <CardTitle className= "cron-builder-bg">Weekly Schedule</CardTitle>
          <Row className= "ron_builder_bordering">
          <Col  sm-auto="true" >
            <FormGroup>  
        <CustomInput type="checkbox" id="MON" label="Monday" value="1" onClick= {this.onCheck}
        defaultChecked= { this.state.value[4].search('1') !== -1 ? true : false} className="text_align_left"/>
        <CustomInput type="checkbox" id="WED" label="Wednesday" value="3" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('3') !== -1 ? true : false} className="text_align_left"/>
        <CustomInput type="checkbox" id="FRI" label="Friday" value="5" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('5') !== -1 ? true : false} className="text_align_left"/>
        <CustomInput type="checkbox" id="SUN" label="Sunday" value="7" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('7') !== -1 ? true : false} className="text_align_left"/>
      </FormGroup>
      </Col>
      <Col sm-auto="true">
      <FormGroup >  
        <CustomInput type="checkbox" id="TUE" label="Tuesday" value="2" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('2') !== -1 ? true : false} />
        <CustomInput type="checkbox" id="THU" label="Thursday" value="4" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('4') !== -1 ? true : false} />
        <CustomInput type="checkbox" id="SAT" label="Saturday" value="6" onClick= {this.onCheck}
        defaultChecked= {this.state.value[4].search('6') !== -1 ? true : false} />
      </FormGroup >  
      </Col>

      </Row>
         <Row >
        
      
      <Col sm={{ size:2, offset:3}} >
      
        <Label >Start Time:</Label>
       
        </Col>
        <Col sm={{ size:0 }}  >
        
        <Input type="select" name="hours" id="hours" onChange={this.onAtHourChange} value={this.state.value[1]} >{this.getHours()}</Input>
        </Col>
        
        
        <Col sm={{ size: 0}} className={"pl-2"}>
      <Input type="select" name="minutes" id="minuts" onChange={this.onAtMinuteChange} value={this.state.value[0]}>{this.getMinutes()}</Input>
      </Col>
      
      
     
      </Row> 
            
           
            
          </CardBody>
       
    );




  }


}

export default Weekly