

import * as ActionTypes from '../actions/ActionTypes';
import {awCreds} from './forms';

export default function (state = awCreds, action){
    switch(action.type){
        case ActionTypes.AW_SELECTED:
          
            return Object.assign({},state, action.payload)

            case ActionTypes.AW_RETRIEVED:
                    
                    return Object.assign({},state, action.payload)

        default:
            return state;
    }
    
}