import React, {Component} from "react";
import {Container, Row, Col, Form, FormGroup, Input, Spinner, Label, Table} from 'reactstrap';
import OtherSearch from './OtherSearch';
import UserSearch from './UserSearch';
import DeviceTabs from './DeviceTabs';
import axios from 'axios';
import {getDevPerUser} from './../../apiCall/airwatch';
import _ from 'lodash';
import {SERVER_URL} from '../../config';


class Airwatch extends Component  {

    state = {
      search: "ImeiNumber",
      deviceId: "",
      deviceData:'',
      disabled:'',
      loading: false,
      message:'',
      radioswitch:'',
      selectedOption: null,
      disuser: true,
      devices: ''
    }

    toggleClass() {
      const currentState = this.state.disabled;
      this.setState({ disabled: !currentState });
  };
  

    
       handleChange = (evt) => {
            const {value,name} = evt.target;
            console.log("event target ", name)
            
            this.setState({
              [name]: value,
              message:''
            }); 
            
            console.log("onChange ", this.state );
          }

           handleChangeSearch = (evt) => {
            const {value} = evt.target;
            
            this.setState({
        
              search: value,
              message:''
              
            }); 
            
            console.log("onChange ", this.state );
          }

          handleChangeRadio1 =(evt) =>{
            if (evt.target.checked) {
              this.setState({
                radioswitch: "radio1",
                deviceData: '',
                message:''
                
              })
              console.log(this.state.radioswitch);
            }
          }

          handleChangeRadio2 =(evt) =>{
            if (evt.target.checked) {
              this.setState({
                radioswitch: "radio2",
                deviceData: '',
                devices: '',
                message:''
              })

              console.log(this.state.radioswitch);
            }
          }

         
          handleChangeUser = selectedOption => {
            
         //   if(this.state.selectedOption){
        if( _.isObject(selectedOption)) {  this.setState({ 
                message: '',
                selectedOption,
                disuser:false});

          
          } else {
              this.setState({ selectedOption,
                message: '',
                disuser: true,
                devices: ''}); 
          
           }
            console.log(`Option selected:`, selectedOption);
          };



      
          getDeviceData = async (search, id) => {

    
            const url = `${SERVER_URL}/uem/searchdevice`;
             
            const data ={
              email: this.props.email,
              sub: this.props.sub,
              search,
              id
             
            }
            
          try { const deviceData = await axios.post(url, data); 
            
            if (_.isString(deviceData.data) && deviceData.data.startsWith("Please56785678")){
              this.setState({
              
                loading: false ,
                message: "You need to go to Settings and set up your credentials"});
                }
       else {
            if(deviceData.data.toString().startsWith('Error:')){
              console.log(deviceData.data);
              this.setState({ loading: false,
                message: deviceData.data});
               
          }else
            this.setState({deviceData,
            loading: false,
           
          })
          console.log("fromapi ",deviceData)
        }
          } catch(err){
           console.log(err);
            
            }
            
        }

        addmessage =(message) => {this.setState({message})}

       onSubmit =  (e) => {
        e.preventDefault();
        e.stopPropagation();
       console.log("state submit ", this.state);
       
       this.setState({ loading: true, message:''}, async () => {
      await  this.getDeviceData(this.state.search, this.state.deviceId)
     
       })
        
       
        console.log("state ",this.state);
        
    }

    onDevChange = (e) => {
      e.persist()
      this.setState({ loading: true, message:''}, async () => {
        console.log("target ", e.target.value);
        await  this.getDeviceData('Serialnumber', e.target.value)
       
         })
    }

    onSubmitUser =  (e) => {
      e.preventDefault();
      
     console.log("state submit ", this.state);
     
     this.setState({ loading: true, message:''}, async () => {
      await  getDevPerUser(this.props.email, this.props.sub, this.state.selectedOption.UserName,this.state.selectedOption.LocationGroupId ).then(
      devices =>{
        if(_.isString(devices.data)){

          
          this.setState({loading: false,
            message:devices.data,
          devices:'' })

        }
       else {
        const filterdevices = devices.data.Devices.filter(item =>{console.log("enr ", item.EnrollmentStatus)
          return item.UserName === this.state.selectedOption.UserName  && item.LocationGroupName === this.state.selectedOption.Group &&  item.EnrollmentStatus === "Enrolled" && item.Platform !== "WindowsMobile"});

        
        if (!_.isEmpty(filterdevices)) {
          console.log("filter ", filterdevices);
      this.setState({loading: false,
        devices:filterdevices
                     // devices:devices.data.Devices 
                    })}
                    else {this.setState({loading: false,
                      message: "no enrolled device found for this user"
                                   // devices:devices.data.Devices 
                                  })}
                  
                  
                  
                  }
      }
    )
   
     })
         
      console.log("state ",this.state);
      
  }

   renderDeviceTable =() => {
       
    return this.state.devices.map((item, i) => {  
       return (  <tr className="my-0 and py-0" key={i} >
            <td className="my-0 and py-0"> <Input type="radio" name="userdev" value={item.SerialNumber} onChange={this.onDevChange}/></td>
         <td className="my-0 and py-0">{item.DeviceFriendlyName}</td>
           <td className="my-0 and py-0">{item.Platform}</td>
      <td className="my-0 and py-0">{item.LocationGroupName}</td>
       </tr> ) 
   
   } )
  }


    render(){
      let loading = this.state.loading;

      console.log("message ", this.state.message)
      console.log("devices ", this.state.devices)
      console.log("devicedata ", this.state.deviceData)


    
      return (
      
      
        <Container >
            <Row>
        <Col sm="4">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
            <h5  className="mb-3 title">Search for device to manage</h5>
         
            <Col sm="4">
      <hr className="style-two"></hr>
      </Col>
         <Form className="mb-4">
            <FormGroup check>
          <Label check>
            <Input type="radio" name="radio1" onChange={this.handleChangeRadio1}/>{' '}
            Search device by username
          </Label>
        </FormGroup>
        <FormGroup check>
          <Label check>
            <Input type="radio" name="radio1" onChange={this.handleChangeRadio2}/>{' '}
            Search by Imei, Serial number ...
          </Label>
        </FormGroup>
        </Form>

        { (this.state.radioswitch === "radio1") ?<UserSearch username={this.state.username} disable={this.state.disuser} handleChange={this.handleChangeUser}  addmessage = {this.addmessage} onSubmit={this.onSubmitUser}/> :'' }
      { (this.state.radioswitch === "radio2") ?<OtherSearch deviceId={this.state.deviceId} handleChange={this.handleChange} handleChangeSearch={this.handleChangeSearch} onSubmit={this.onSubmit}/> :'' }
     
       
           <Row>
           <h2 >{this.state.message}</h2>
           </Row>
           <Row>
          <Col md={3}></Col>
          <Col>
           {loading ? <Spinner  animation="border" variant="warning" /> : "" }
           </Col>
           </Row>
          
           {(_.isArray(this.state.devices)) ?     
      <Table borderless size="sm" className='table-condensed' style={{width: '80%'}}>
      <thead>
        <tr className="my-0 and py-0">
          <th className="my-0 and py-0 "></th>
          <th className="my-0 and py-0 " >Friendly Name</th>
          <th className="my-0 and py-0 ">Platform</th>
          <th className="my-0 and py-0 ">OG</th>     
        </tr>
        {this.renderDeviceTable()}
      </thead>
      <tbody>
     
      
      </tbody>
    </Table> :''}
    
    {(this.state.deviceData && this.state.deviceData.data !== "Unprocessable Entity") ? <DeviceTabs  deviceData={this.state.deviceData} email={this.props.email} sub={this.props.sub} awcreds={this.props.awcreds}/>:"" }
       </Container >

     



    )
    }
 }
  

export default Airwatch;

