import React, { Component } from 'react';
import {Button, Spinner, Container, Row, Form, FormGroup, Col, CustomInput} from 'reactstrap';
import {getToken, axiosListReportRec, searchAllReports, axiosGetLink3} from '../../apiCall/intelli';
import { fetchData} from '../../apiCall/midatalakes';
import _ from 'lodash';

class ReportsTransfer extends Component {

  _isMounted = false;

    state = {
      reports: [],
      reportId:'',
      link: '' ,
      date: '',
      id: '' ,
      message: '' ,
      disabled: true,
      time:'',
      loading: false,
      token:'',
      isSelected:false,
      check: "check All",
      checked: false,
      markedreports: [],
      checkedBoxes: [],
      links: [],
      color:"red",
      disabled2:false

    };
   
  toggleClass() {
    const currentState = this.state.disabled;
    this.setState({ disabled: !currentState });
};

toggleClassTransfer() {
  const currentState = this.state.disabled2;
  this.setState({ disabled2: !currentState });
};


async componentDidMount(){

  fetchData(this.props.email, this.props.sub, this.props.miretrieve2Creds );
  this._isMounted = true;
  this.setState({ loading: true}, async () => {

    console.log("ima in did mount")
  const token = await getToken(this.props.email,this.props.sub);
  console.log("ima in did mount token", token)
  if (token.startsWith("Please56785678")){
    this.setState({loading: false ,
      color:"red",
      message: "You need to go to Settings and set up your credentials"});
      }
      else{
        if (this._isMounted){
     this.setState({token});
     
     
      const reporting = await searchAllReports(this.props.email,this.props.sub, this.state.token); 
      if (_.isString(reporting.data) ){
        this.setState({loading: false ,
          message: reporting.data});
          }
      else {
         let allReports =  reporting.data.map( el => { return {name: el.name, id: el.id}} );

         this.setState({reports: allReports,
          loading: false });
         console.log("all report", allReports)
      }
    }
    }
  });

  }


  componentWillUnmount() {
    this._isMounted = false;
  }

    handleCheckedAll =(evt) => {

      if (evt.target.checked) {

        const listReports = this.state.reports
        
       this.setState({checkedBoxes: listReports,
        check: "Uncheck All",
        message:""})

      } else {
        this.setState({checkedBoxes: [],
          check: "Check All",
          message:""})
      }
    }


    handleCheckbox = (e, v) => {
      const checkedBoxes = [...this.state.checkedBoxes];
      
      if(e.target.checked) {
       checkedBoxes.push(v)
      } else {
        const index = checkedBoxes.findIndex((ch) => ch.id === v.id);
        checkedBoxes.splice(index, 1);
      }
      this.setState({checkedBoxes,
        message:""});
      
  
    }
  

  
    onSubmit = async (e) => {
      e.preventDefault();
      if (this.props.mi2creds.azureconnectstring){
      if (this.state.checkedBoxes.length>0){
      this.toggleClass();
      this.setState({ loading: true}, async () => {

    
     const getData = async () => {
        return  await Promise.all(this.state.checkedBoxes.map(async item => await axiosListReportRec(this.props.email,this.props.sub, this.state.token, item.id)))
     }
      
   
     
     
      getData().then(data => {console.log("myarray ",data); return data.map(item => item.filter(i => i.status === "COMPLETED").reduce(function (a, b) { return a.created_at > b.created_at  ? a : b; })) })
      .then(data => {console.log("myarray2 ",data); this.setState({ loading: false,
                                                                markedreports: data})})
  
   
        }) } else this.setState({
          color:"red",
          message: "You need to select a least one report"})

      } else this.setState({
        color:"red",
        message: "You need to set up your Data Lake Gen 2"})
        if (this.state.disabled2) this.toggleClassTransfer();
  }


   onTransfer = async () =>{
    this.toggleClassTransfer();
    
    this.setState({ loading: true}, async () => {
     

   const getData = async () => {
    return  await Promise.all(this.state.markedreports.map(async item => await axiosGetLink3(this.props.email,this.props.sub,this.state.token, item.id)))
  }

  getData().then(data => {
    console.log("last step",data);
    console.log(data.length);
    this.setState({ loading: false,
    links:data,
    color: "green",
    message:"reports have been transfered to Data Lake"}
      );

    console.log("links ", this.state.links )
  });
  
  this.toggleClass();
    })

}

    render() {
    
    let loading = this.state.loading;
      return (
        <Container>
           <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
           <Col sm="8" className="text-center">
      <h5 className="mb-3 title" style={{textAlign: "center"}}>Select reports to be transfered to Data Lake Gen2</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
          

           
        <Form onSubmit={this.onSubmit}>
        <Col sm="8">
        <FormGroup>
       
        <CustomInput type ="checkbox" id= "checkall" onChange={this.handleCheckedAll} label={this.state.check} />
       <hr></hr>
        {this.state.reports.map((v) =>  <CustomInput type ="checkbox" 
        key={v.id} id={v.id} label={v.name} 
        checked={this.state.checkedBoxes.find((ch) => ch.id === v.id)}
         onChange={(e) => this.handleCheckbox(e, v)}/>)}
        
        
        </FormGroup>
        <div className="mt-4 mb-4">
        <Button type="submit"  color="info" variant="success" size="sm" block disabled={!this.state.disabled}>Submit selection</Button>
        </div>
        </Col>
      </Form> 
       
      <Row >
         <Col sm="8" className="text-center">
      {loading ? <Spinner className="mx-auto" animation="border" variant="warning" /> : "" }
      </Col>
      </Row>
      
      <Col sm="8">
      {(this.state.markedreports.length>0 && !this.state.disabled) ? <Button  variant="outline-dark" size="sm" block  onClick={this.onTransfer} disabled={this.state.disabled2}>Transfer Now</Button>: ""}
      
      <Row>
      <h2 className="mt-1" style={{color:this.state.color}}>{this.state.message}</h2> 
      </Row>
      </Col>
      
      
      
      </Container>
      );
    }
  }

  export default ReportsTransfer;