import React, {useState, useEffect} from "react";
import {Container, Row, Col, Form, FormGroup, Input, Button, Label, Spinner} from 'reactstrap';
import { useAuth0 } from "../../Auth0/react-auth0-spa";
import axios from 'axios';
import base64 from 'react-native-base64';
import {fetchAwData, testCreds} from '../../apiCall/airwatch';
import _ from 'lodash';
import {SERVER_URL} from '../../config';

const Airwatch = (props) =>  {

    const { user } = useAuth0();


    const [state, setState] = useState({
      email: user.email,
      sub: user.sub,
      loading: false,
      disabled2: true
        
    })

   // const [trigger, setTrigger] = useState(0)
    
  const [isOpen, setOpen] = useState(true)
  const toggleEdit= () => setOpen(!isOpen)

  const [isDisabled, setDisabled] = useState(false);
  const toggleDisabled = () => setDisabled(!isDisabled);


     useEffect( () => {
        // code to run on component mount
      
      
        fetchAwData(user.email, user.sub, props.retrieveCreds );
    
      }, [])

    
    
      const handleChange = (evt) => {
       // store in redux
        props.selectCreds(
         
          evt.target.name,evt.target.value
          )

          setState({
               ...state,
               disabled2: true,
               message: "" ,
               loading: false   
          })
  
      }

      const stripTrailingSlash = (str) => {
         
        if (str.endsWith('/api/'))
        {return   str.slice(0, -5) }
       else if (str.endsWith('/api'))
        {return   str.slice(0, -4) }      
        else if (str.endsWith('/'))
        {return   str.slice(0, -1) }
        else {return str}
    };

      const saveAwCreds = async () => {

       console.log("lgis from save " , state.lgid)
        const url = `${SERVER_URL}/airwatch`;
        const awencoded = base64.encode(`${_.trim(props.awcreds.awadmin)}:${props.awcreds.awpassword}`)
        let newurl = _.trim(stripTrailingSlash(props.awcreds.awurl.toLowerCase()))

        const data ={
          email: user.email,
          sub: user.sub,
          awurl: newurl,
          apikey: _.trim(props.awcreds.apikey),
          awadmin: _.trim(props.awcreds.awadmin),
          awpassword: props.awcreds.awpassword,
          awencoded,
         // lgid: state.lgid
         lgid: props.awcreds.lgid
        }
        
      try { const jwt = await axios.post(url, data); 
        console.log("jwtStatus ",jwt.status);
        
        if (jwt.status === 200){
            console.log("toto");

                 
                 toggleEdit();
                 toggleDisabled(); 
            console.log("state 2",isOpen);
          
           
           
              //save in redux props
              props.retrieveCreds({awurl: props.awcreds.awurl,
                apikey: props.awcreds.apikey,
                awadmin: props.awcreds.awadmin,
                awpassword: props.awcreds.awpassword,
             // lgid: state.lgid
            });


              return jwt;
          }
          else {
           
           
            }

      
      } catch(err){
       console.log(err);
        
        }
        
    }

    const testCred = async () =>{
      console.log("toto est grand");
      const awencoded = base64.encode(`${_.trim(props.awcreds.awadmin)}:${props.awcreds.awpassword}`)
      console.log("encoded", awencoded);
      setState({
        ...state,
        message:'',
      loading: true});

        let newurl = stripTrailingSlash(props.awcreds.awurl.toLowerCase())
     console.log("new test", newurl);
     console.log("admin", props.awcreds.awadmin);
      await testCreds(_.trim(newurl), _.trim(props.awcreds.apikey),awencoded, _.trim(props.awcreds.awadmin)).then(
        admin => {  console.log(admin.data[0]);
          if (_.isString(admin.data)){
            setState({
              ...state,
              message: admin.data,
              disabled2: true,
              color: 'red',
              loading: false
            })
          }
          else{ 
            props.retrieveCreds({lgid: props.awcreds.lgid})
            setState({
            ...state,
           // lgid: admin.data[0].LocationGroupId,
            disabled2: false,
            color: 'green',
          message:'Test succesfull',
          loading: false
         })
        
        
        }
      
        })
      
    }


      const onSubmit =  async (e) => {
        e.preventDefault();
        e.stopPropagation();
      
        console.log("try to save settings");
        saveAwCreds();

      /*  await getAdmins(user.email, user.sub, props.awcreds.awadmin).then(
          admin => {  console.log(admin.data);

          }
        ) */
        setState({...state,
          disabled2: true,
        message:'' })
        
        
        console.log("state ",state);
       
    }


    const Test = () => {return <Row><Col sm ="4"><Button type="button" color="primary" block size="sm" onClick = {testCred}>Test</Button></Col><Col sm ="4"><Button type="submit" value="save" block disabled={state.disabled2} color="info" size="sm">Submit</Button></Col></Row>}
    

    return (
      
        <Container >
            <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
       
           <Row >
           
      <Col sm="8" className="text-center">
      <h5 className="mb-3 title" >WS1 UEM API credentials</h5>  
       </Col>
       </Row>
       <Row>
        <Col sm="8">
      <hr className="style-two"></hr>
      </Col>
        </Row>
           
        <Form onSubmit={onSubmit} > 
        <Col sm="8">
        <FormGroup>
            <Label for="awurl"> Api Tenant Url:  </Label>
                <Input type="text" className="form-control" id="awurl" name="awurl" value={props.awcreds.awurl.trim()} onChange={handleChange} required disabled={!isDisabled}/>
                </ FormGroup >
       <FormGroup>
            <Label for="apikey">API Key:  </Label>
                <Input type="text" className="form-control" id="apikey" name ="apikey" value={props.awcreds.apikey.trim()} onChange={handleChange} required disabled={!isDisabled}/>
                </ FormGroup >
                <FormGroup>
            <Label for="awadmin">Admin Username:  </Label>
                <Input type="text" className="form-control" name ="awadmin" value={_.trim(props.awcreds.awadmin)} onChange={handleChange} required disabled={!isDisabled}/>
                </ FormGroup >
                
            <FormGroup>
            <Label for="awpassword">Password: </Label>
                <Input type="password" className="form-control" name ="awpassword" value={props.awcreds.awpassword} onChange={handleChange} required disabled={!isDisabled}/>
                </FormGroup >
              <FormGroup>
                {(!isOpen)?<Test/> : 
                 <Row>
                 <Button color="primary" size="sm" block active  onClick = {()=>{toggleDisabled(); toggleEdit()}}>Edit</Button>
                 </Row>}
              </FormGroup >
                </Col>
       </Form >
       
           <Row>
             <Col sm="2"></Col>
             {state.loading ? <Spinner  animation="border" variant="warning" /> : "" }
             <Col sm="auto"> <h2 style ={{color: state.color}}>{state.message}</h2></Col>
           </Row>
       
       </Container >
   
    )
 }
  

export default Airwatch;

